.head{
   
  position: absolute;
  overflow: hidden;
  height: 100%;
  width: 100%;
  z-index: -1;

  
}


.header-menu {
    display: flex;
    justify-content: space-between; /* Items spaced evenly */
    align-items: center; /* Center items vertically */
  }

.upar > li > button:hover{
  color: white;
  
}
  
   .upar {
    display: flex;
    list-style-type: none;
   
    width: 100%; /* Ensures the list takes full width */
  } 

  .upar > li {
    
    flex: 1; /* Each item takes an equal portion of available space */
    margin: 0 5px; /* Adjust horizontal spacing between items */
    text-align: center; /* Center text */
  }
  
  .upar > li > button{
    cursor: pointer;
    background-color: royalblue;
    border: none;
  }

  .head > h1{
    background-color: indigo;
  }

  .Desc{
    align-items: center;
    text-align : center;
    display:flex;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 100;
    color: white;
    margin: left 25%;
    margin: right 25%;
     justify-content: center; /* Items spaced evenly */
    
    


  }

  /* #root{
    background-color:indigo;
  } */

   .movie{
    max-width: 100%;
    max-height: 100%;
    cursor:pointer;
    margin: 20px;
     
    
    
    
    
    
  } 

  .movie:hover {
    background-color: yellow;
  }
  .movie > img{
  
    border: 1px solid #ccc; /* Optional: Add a border for visualization */
    margin: 10px; /* Optional: Add padding around the content */
    /* Other styles for .movie container */
  
    /* Set the maximum width and height for the image */
    width: 227px; /* Ensure the image doesn't exceed the container width */
    max-height: 100%; /* Set a maximum height for the image */
  
    /* Ensure the image fits inside its container */
     /* Maintain aspect ratio and cover the container */

  }

  .movie > h3 {
    text-align: center;
  }


  .movie-list{
    display: flex;
    flex-wrap: wrap;
  }

  .Net{  
    width: 100%;
    position: absolute;
    z-index:1;
     padding-left: 8px;
    padding-bottom: 2px;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));   



    
    

  }

   .Net>img{
    width:200px;
  } 

  .email{
    padding:10px;
    margin:10px;
    background-color: gray;
    

    
    
    
    

  }

  .pw{
    
    padding:10px;
    margin:10px;
    background-color: gray;
    text-decoration: white;


    
  }
  .sign{
    padding:10px;
    margin:10px;
    margin-top:10px;
    width:92%;
    
    background-color: red;
    border-radius: 10px;
    border-color: white;
    color:white;
    border: white;
    
  
    
    
  }

  .sign:hover{
    background-color: white;
    color: black;
    cursor: pointer;
    border: white;
    border-radius: 10px;
  }

.signin{
   
   background-color: black;
   padding:20px;
   border-radius: 2%;
   opacity: 0.8;
   top: 50%;
   left: 50%;
   position: absolute;
   transform: translate(-50%, -50%);
   
  
}

.sign-text{
  color:white;
  margin:10px;
  font-weight: bold;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size:20px;
}

